.btn {
  letter-spacing: .1em;
  color: #ff6263;
  cursor: pointer;
  background-color: #0000;
  border: 1px ridge #9e9e9e;
  border-radius: 2.5em;
  outline: none;
  width: 10em;
  height: 2.8em;
  font-size: 16px;
  font-weight: bold;
  transition: all 1s;
  position: relative;
}

.btn:after {
  content: "";
  transform-origin: center;
  background-color: #fff;
  width: 50%;
  height: 40%;
  transition: all .5s;
  position: absolute;
  top: -10px;
  left: 25%;
}

.btn:before {
  content: "";
  transform-origin: center;
  background-color: #fff;
  width: 50%;
  height: 40%;
  transition: all .5s;
  position: absolute;
  top: 80%;
  left: 25%;
}

.btn:hover:before, .btn:hover:after {
  transform: scale(0);
}

.btn:hover {
  letter-spacing: 1.7px;
  color: #585858;
  border: 1px ridge #7575754d;
  transition: all 1.5s;
  box-shadow: inset 0 0 25px #7575754d;
}

.nav__button, .btn, link {
  color: #9e9e9e;
  font-size: 12px;
  display: none;
}

@media screen and (width >= 767px) {
  .nav__button, .btn, link {
    display: initial;
    margin-left: 91px;
  }
}

@media screen and (width >= 1200px) {
